// Контейнер
.container {

  max-width: 320px;
  padding-right: 20px;
  padding-left: 20px;
  margin-left: auto;
  margin-right: auto;

  @include tablet {
    max-width: 768px;
    padding-right: 32px;
    padding-left: 32px;
  }

  @include desktop {
    max-width: 1200px;
    padding-right: 16px;
    padding-left: 16px;
  }
}